body {
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif;
}

a {
  color: inherit;
}

.page__main {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
}

.page__main h1,.page__main h2,.page__main h3,.page__main h4 {
  font-family: "Abril Fatface", cursive;
  letter-spacing: 0.2px;
}

.page__main .section__list--movies {
  width: 80vw;
  flex-grow: 2;
  order: 1;
  text-align: center;
  transition: 0.2 ease;
}

.page__main .section__list--movies .search__shows {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #adadad;
  padding: 0 0 45px 0;
  transition: 0.2 ease;
}

.page__main .section__list--movies .search__movie {
  width: 300px;
  border: 1px solid gray;
  margin: 15px;
  font-size: 16px;
  padding: 0 0 3px 0;
}

.page__main .section__list--movies .btn__search {
  width: 300px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  border: 0.5px solid lightgray;
  font-size: 18px;
}

.page__main .section__list--movies .btn__search:hover {
  background-color: rgba(222, 145, 172, 0.8);
  color: white;
  border: 0.5px solid transparent;
}

.page__main .section__list--movies .btn__search:focus {
  background-color: rgba(222, 145, 172, 0.8);
  color: white;
}

.page__main .section__list--movies .show__shows {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  overflow: scroll;
}

.page__main .section__list--movies .show__shows .show__list--item {
  width: 220px;
  height: 350px;
  padding: 10px;
  margin: 10px 40px;
  display: flex;
  flex-direction: column;
  box-shadow: 1px 1px 10px 0px rgba(173, 173, 173, 0.6);
  cursor: pointer;
  position: relative;
  transition: ease 0.2s;
}

.page__main .section__list--movies .show__shows .show__list--item img {
  width: 195px;
  height: 280px;
  margin: auto;
}

.page__main .section__list--movies .show__shows .show__list--item .show__item--info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #de90a9;
  color: white;
  overflow: hidden;
  width: 100%;
  height: 10%;
  transition: 0.5s ease;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  margin-top: 10px;
  padding: 0 10px 0px 10px;
  font-weight: 400;
  font-family: 'Playfair Display', serif;
  line-height: 1.2;
}

.page__main .section__list--movies .show__shows .show__list--item .show__item--info .overlay__text {
  font-size: 16px;
  font-weight: bold;
  margin: 4.5% 0;
}

.page__main .section__list--movies .show__shows .show__list--item .show__item--info:hover,.page__main .section__list--movies .show__shows .show__list--item .show__item--info:focus,.page__main .section__list--movies .show__shows .show__list--item .show__item--info .overlay {
  height: 90%;
  overflow: scroll;
}

.page__main .section__list--movies .show__shows .fav__show--style {
  width: 220px;
  height: 350px;
  padding: 10px;
  margin: 10px 40px;
  display: flex;
  flex-direction: column;
  box-shadow: 1px 1px 10px 0px rgba(173, 173, 173, 0.8);
  background-color: #de90a9;
  color: white;
  background-position: center;
  cursor: pointer;
  position: relative;
  transition: ease 0.2s;
}

.page__main .section__list--movies .show__shows .fav__show--style img {
  width: 195px;
  height: 280px;
  margin: auto;
  outline: 105px solid rgba(222, 144, 169, 0.5) !important;
  outline-offset: -105px;
  transition: all linear 0.2s;
}

.page__main .section__list--movies .show__shows .fav__show--style .show__item--info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #de90a9;
  color: white;
  overflow: hidden;
  width: 100%;
  height: 10%;
  transition: 0.5s ease;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  margin-top: 10px;
  padding: 0 10px 0px 10px;
  font-weight: 400;
  font-family: 'Playfair Display', serif;
  line-height: 1.2;
}

.page__main .section__list--movies .show__shows .fav__show--style .show__item--info .overlay__text {
  font-size: 16px;
  font-weight: bold;
  margin: 4.5% 0;
}

.page__main .section__list--movies .show__shows .fav__show--style .show__item--info:hover,.page__main .section__list--movies .show__shows .fav__show--style .show__item--info:focus,.page__main .section__list--movies .show__shows .fav__show--style .show__item--info .overlay {
  height: 90%;
}

.page__main aside {
  text-align: center;
  box-shadow: 1px 1px 10px rgba(192, 192, 192, 0.5);
  height: 100vh;
  margin-top: 0;
  position: relative;
  overflow: hidden;
}

.page__main aside .fav__section--title {
  padding: 20px 0;
}

.page__main aside .btn__remove--all {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  border: 1px solid lightgray;
  font-size: 12px;
  margin: 0 5px;
  padding: 3px;
}

.page__main aside .btn__remove--all:hover {
  background-color: rgba(222, 145, 172, 0.8);
  color: white;
}

.page__main aside .btn__remove--all:focus {
  background-color: rgba(222, 145, 172, 0.8);
  color: white;
}

.page__main aside .section__fav--movies {
  width: 20vw;
  height: 90vh;
  padding: 20px;
  order: 0;
  text-align: center;
  overflow: scroll;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 10px;
}

.page__main aside .section__fav--movies li {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin: 5px 0 5px 0;
}

.page__main aside .section__fav--movies li h4 {
  margin: 10px 0;
  line-height: 1.3;
}

.page__main aside .section__fav--movies li a {
  color: inherit;
  border-bottom: 1px dashed #de91ac;
  transition: all 0.2s ease;
}

.page__main aside .section__fav--movies li a:hover {
  border-bottom: 1px dashed black;
  color: #de91ac;
}

.page__main aside .section__fav--movies li .btn__remove--single {
  position: absolute;
  top: 0;
  background-color: black;
  color: white;
  border-radius: 100%;
  border: 2px solid rgba(255, 255, 255, 0.85);
}

.page__main aside .section__fav--movies li .btn__remove--single:hover {
  background-color: #de91ac;
}

.page__main aside .section__fav--movies li img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 120px;
}

.hidden {
  display: none;
}

.full_screen {
  height: 100vh;
  font-size: 4vh;
  background: url(https://images.unsplash.com/photo-1565098772267-60af42b81ef2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3484&q=80) no-repeat;
  background-size: cover;
  background-position: center;
  color: white;
  transition: ease 0.2s;
  padding: 0;
}

.medium_screen {
  height: 80vh;
  transition: ease 0.2s;
}

@media all and (min-width: 1280px) {
  .page__main .section__list--movies .show__shows .show__list--item {
    width: 250px;
    height: 400px;
  }

  .page__main .section__list--movies .show__shows .show__list--item img {
    width: 210px;
    height: 295px;
  }

  .page__main .section__list--movies .show__shows .fav__show--style {
    width: 250px;
    height: 400px;
  }

  .page__main .section__list--movies .show__shows .fav__show--style img {
    width: 210px;
    height: 295px;
  }

  .page__main aside .section__fav--movies li h4 {
    margin: 0 20px;
  }

  .page__main aside .section__fav--movies li {
    flex-direction: row;
  }

  .page__main aside .section__fav--movies li img {
    width: 150px;
  }
}

@media all and (max-width: 500px) {
  .page__main .section__list--movies {
    width: 70vw;
  }

  .page__main .section__list--movies .search__movie {
    width: 150px;
  }

  .page__main .section__list--movies .btn__search {
    width: 150px;
    font-size: 16px;
  }

  .page__main .section__list--movies .show__shows .show__list--item {
    width: 150px;
    height: 240px;
  }

  .page__main .section__list--movies .show__shows .show__list--item img {
    width: 130px;
    height: 185px;
  }

  .page__main .section__list--movies .show__shows .show__list--item .show__item--info .overlay__text {
    font-size: 14px;
  }

  .page__main .section__list--movies .show__shows .show__list--item .show__item--info:hover,.page__main .section__list--movies .show__shows .show__list--item .show__item--info:focus,.page__main .section__list--movies .show__shows .show__list--item .show__item--info .overlay {
    height: 88%;
  }

  .page__main .section__list--movies .show__shows .fav__show--style {
    width: 150px;
    height: 240px;
  }

  .page__main .section__list--movies .show__shows .fav__show--style img {
    width: 130px;
    height: 185px;
  }

  .page__main .section__list--movies .show__shows .fav__show--style .show__item--info .overlay__text {
    font-size: 14px;
  }

  .page__main .section__list--movies .show__shows .fav__show--style .show__item--info:hover,.page__main .section__list--movies .show__shows .fav__show--style .show__item--info:focus,.page__main .section__list--movies .show__shows .fav__show--style .show__item--info .overlay {
    height: 88%;
  }

  .page__main aside .section__fav--movies {
    width: 30vw;
    align-items: center;
  }

  .page__main aside .section__fav--movies li img {
    width: 60px;
    height: 85px;
  }
}
